/* header */
.header {
  position: relative;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  height: 12vw;
}

.title {
  text-align: left;
  font-size: 3vw;
  text-decoration: none;
  padding-left: 7%;
  bottom: -2vw;
  position: relative;
  user-select: none; /* Standard syntax */
}

@media only screen and (max-width: 600px) {
  .title {
    font-size: 5vw;
  }
}

/* home page */
.search-links-left {
  padding-left: 7%;
  text-align: left;
  display: inline-block;
  font-size: 2vw;
  left: 0%;
  position: fixed;
}

.search-links-right {
  padding-right: 7%;
  text-align: right;
  display: inline-block;
  font-size: 2vw;
  right: 0%;
  position: fixed;
}

.ascii-art {
  position: absolute;
  bottom: 0%;
  left: 33%;
}

pre {
white-space: pre-wrap;
}

@media only screen and (max-width: 600px) {
  .search-links-left {
    font-size: 4vw;
    padding-top: 5vw;

  }
  
  .search-links-right {
    font-size: 4vw;
    padding-top: 5vw;

  }
}

/* me page */

.text-holder {
  padding: 1vw;
  padding-left: 5%;
  padding-right: 5%;
  text-align: left;
}

.small-link:link  {
  color:rgb(254, 75, 120) !important;
}
.small-link:visited {
  color:rgb(211, 3, 103) !important;
}
.small-link:hover {
  color: rgb(290, 14, 61) !important;
  text-decoration: underline;
}

@media only screen and (max-width: 400px) {
  .link-holder {
    padding-top: 10vw;
  }
}

/* cv page */

.pdf-viewer {
  width: 70%;
  height: 50vw;
}

.download-image {
  width: 2vw
}

.too-small {
  display: none;
}

@media only screen and (max-width: 400px) {
  .download-image {
    width: 4vw;
  }
  .pdf-viewer {
    display: none;
  }
  .too-small {
    display: block;
  }
}

@media (max-aspect-ratio: 3/2) {
  .pdf-viewer {
    height: 100vw;
  }
}

/* diss page */

.ui-image {
  width: 100%;
  height: 100%;
}

.subtitle {
  text-decoration: underline;
  font-size: 17px;
}


.inline-structure {
  padding: 1vw;
  display: inline-block;
  width: 40%;
  vertical-align: top;
}

@media only screen and (max-width: 1100px) {
  .subtitle {
    padding-top: 10vw;
  }
}

@media only screen and (max-width: 600px) {
  .inline-structure {
    display: block;
    width: 90%;
    left: 5%;
    position: sticky;
    
  }
}

/* other */


.left-align {
  text-align: left;
}

.back-button {
  left: 4%;
  position: absolute;
  display: inline-block;
}

@media only screen and (max-width: 420px) {
  .back-button {
    display: none;
  }
}

.small-back-button {
  left: 4%;
  position: absolute;
  display: none;
}

@media only screen and (max-width: 420px) {
  .small-back-button {
    display: inline-block;
    padding: 5px 10px;
  }
}



/* General css writes */

hr {
  color: white;
}

a {
  text-decoration: none;
}

button {
  border-radius: 2px;
  font-family: 'Montserrat', sans-serif;
  appearance: none; 
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  margin: 15px 0;
  padding: 10px 20px;
  background: #eee;
  letter-spacing: 0.2em;
  line-height: 100%;
  border-radius: 3px;
  opacity: 0.9;
  transition: opacity 0.3s ease;
}

button:hover {
  opacity: 1;
}

button:active {
  transform: scale(0.98); 
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24); 
}

* {
  caret-color: transparent;
}